  flex-direction: column; }

.footer-aside__info {
  position: absolute;
  bottom: -53px;
  left: 222px;
  text-transform: uppercase;
  font-family: "PT Mono", "Arial", monospace;
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  opacity: 0.5; }

.footer-aside__social {
  margin-top: 50px; }

@media (max-width: 1023px) {
  .footer-aside {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 26px;
    border-bottom: 1px solid #e5e5e5; }
  .footer-aside__logo-image {
    width: 216px;
    height: 52px; }
  .footer-aside__info {
    position: static; } }

@media (max-width: 767px) {
  .footer-aside {
    flex-direction: column; } }

.social-list {
  width: 112px;
  list-style: none;
  display: flex;
  justify-content: space-between;
  padding: 0;
  margin: 0; }

.footer-menu {
  display: flex;
  flex-direction: column;
  flex-grow: 1; }

.footer-menu h3 {
  font-weight: normal;
  font-size: 15px;
  line-height: 16px;
  margin-top: 0;
  margin-bottom: 25px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #1b2d37; }

.footer-menu__toggle {
  display: none; }

@media (max-width: 1023px) {
  .footer-menu {
    flex-grow: 0;
    margin: 0; } }

@media (max-width: 767px) {
  .footer-menu--closed .menu-list__item {
    display: none; }
  .footer-menu--nojs .menu__toggle {
    display: none; }
  .footer-menu--nojs {
    position: static;
    min-height: 0; }
  .footer-menu--nojs .menu-list__item {
    display: block; }
  .footer-menu__toggle {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 2;
    display: block;
    width: 16px;
    height: 16px;
    background-color: transparent;
    cursor: pointer;
    border: none; }
    .footer-menu__toggle--opened {
      background-image: url("../../img/icon-menu-open.svg");
      background-repeat: no-repeat;
      background-position: center; }
    .footer-menu__toggle--closed {
      background-image: url("../../img/icon-menu-close.svg"); } }

.footer-list {
  list-style: none;
  font-family: "PT Mono", "Arial", monospace;
  font-size: 15px;
  line-height: 26px;
  padding: 0;
  opacity: 0.5; }

.footer-list--menu {
  height: 120px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin-top: 0;
  padding-right: 20px; }

.footer-list--menu li {
  width: 60%; }

@media (max-width: 1023px) {
  .footer-list--menu {
    height: 100%;
    flex-wrap: nowrap; } }

.footer-address {
  margin-left: 120px;
  margin-right: 2px; }

.footer-address h3 {
  font-weight: normal;
  font-size: 15px;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #1b2d37;
  margin-top: 0;
  margin-bottom: 25px; }

@media (max-width: 767px) {
  .footer-address h3 {
    position: relative; }
    .footer-address h3::after {
      position: absolute;
      content: url("../../img/icon-menu-open.svg");
      bottom: 5px;
      right: 0; } }

.copyright {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  text-transform: uppercase;
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  opacity: 0.5; }

.copyright__item:first-child {
  margin-right: 38px; }

@media (max-width: 1023px) {
  .copyright {
    flex-direction: column; }
  .copyright__item:first-child {
    display: none; } }

@media (max-width: 767px) {
  .copyright {
    flex-direction: column;
    align-items: center; }
  .copyright__item {
    padding-top: 10px;
    padding-bottom: 10px; } }

@keyframes bounce {
  0% {
    transform: translateY(-2000px); }
  70% {
    transform: translateY(30px); }
  90% {
    transform: translateY(-10px); }
  100% {
    transform: translateY(0); } }

@keyframes shake {
  0%,
  100% {
    transform: translateX(0); }
  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translateX(-10px); }
  20%,
  40%,
  60%,
  80% {
    transform: translateX(10px); } }

.modal {
  position: fixed;
  width: 440px;
  z-index: 5;
  padding: 25px;
  border: 1px solid #c6c6c6;
  display: none;
  font-size: 17px;
  box-shadow: 0 30px 50px rgba(0, 0, 0, 0.7);
  background-color: #00b2ff;
  color: #ffffff; }

.modal-show {
  display: block;
  animation: bounce 0.6s; }

.modal-error {
  animation: shake 0.6s; }

.modal__overlay {
  position: fixed;
  z-index: 4;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); }

.modal-overlay-show {
  display: block; }

.modal-order-bell {
  top: 50px;
  left: 0;
  right: 0;
  margin: 0 auto;
  box-sizing: border-box;
  width: 440px;
  height: 605px; }

.modal-close {
  position: absolute;
  top: 16px;
  right: 17px;
  width: 22px;
  height: 22px;
  font-size: 0;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  z-index: 103; }

.modal-order-bell h2,
.modal-order-bell p {
  display: flex;
  flex-direction: column;
  justify-content: flex-start; }

.modal-order-bell h2 {
  margin-top: 8px; }

.modal-order-bell p {
  margin-bottom: 19px; }

.modal-order-bell input[type="tel"],
.modal-order-bell input[type="text"] {
  height: 52px; }

.modal-order-bell input[type="tel"] {
  margin-top: 30px; }

.modal-order-bell textarea {
  height: 104px; }

.checkbox {
  width: 100%;
  margin-left: -10px; }

.checkbox label {
  padding-top: 6px;
  padding-left: 50px; }
