.button {
  width: 255px;
  display: flex;
  align-items: center;
  justify-content: center;

  padding: 14px 0;

  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;

  background-color: transparent;
  border: 1px solid black;
  border-radius: 5px;
  box-sizing: border-box;

  &:hover {
    background-color: #00b2ff;
  }

  &:active {
    color: #003e59;
  }
}

.button--call {
  width: 180px;
  border-color: #ffffff;
  color: #ffffff;
  padding: 14px 13px;
  margin-top: 3px;
  margin-left: 26px;
  height: 45px;
}

.button--advice {
  width: 409px;
  background: #00b2ff;
  color: #ffffff;
  margin-left: 95px;
  margin-top: 21px;

  font-size: 16px;
  line-height: 19px;

  padding: 16px 0 19px;

  &:hover {
    color: #ffffff;
    background-color: #355161;
  }

  &:active {
    color: #fafafa;
    background-color: #8d9498;
  }
}

.button--about {
  width: 180px;
  border-color: #00b2ff;
  color: #00b2ff;
  margin-top: 20px;

  &:hover {
    color: #ffffff;
  }

  &:active {
    color: #ffffff;
  }
}

.button--question {
  width: 49%;
  padding: 13px;
  border-color: #ffffff;
  flex: 1 1 auto;

  &:hover {
    color: #000000;
  }

  &:active {
    color: #c4baba;
  }
}

.button--order-bell {
  width: 100%;
  border-color: #ffffff;
  color: #ffffff;
  padding: 13px;
  margin-bottom: 30px;
}

@media (max-width: $tablet-width) {
  .button--call {
    display: none;
  }

  .button--advice {
    width: 379px;
    padding: 17px 14px;
    margin-left: 51px;
    margin-top: 36px;
  }

  .button--question {
    width: 49%;
  }
}

@media (max-width: $mobile-width) {
  .button--advice {
    width: 287px;
    font-size: 16px;
    line-height: 19px;
    margin-left: 0;
    padding-top: 22px;
    padding-bottom: 22px;
    position: relative;

    &::before {
      position: absolute;
      content: "Бесплатная консультация";
    }
  }

  .button__text {
    display: none;
  }

  .button--about {
    margin-top: 15px;
  }

  .button--question {
    width: 100%;
  }
}
